body {
  margin: 0px;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    32deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 13%,
    rgba(0, 212, 255, 1) 100%
  );
  font-family: monospace;
  /* cursor: none; */
  overflow: hidden;
}
#canvas1 {
  position: absolute;
  display: flex;
  right: 0px;
}
.cursor {
  position: absolute;
  width: 30px;
  height: 30px;
  pointer-events: none;
  border-radius: 50%;
  mix-blend-mode: difference;
  background: #fff;
  z-index: 1000;
}
.hoverToolTip {
  background-color: rgba(99, 213, 109, 0.712);
  border-radius: 30px;
  width: fit-content;
  height: 0px;
  position: absolute;
  top: 100px;
  display: flex;
  opacity: 1;
  align-items: center;
  justify-content: left;
  padding-left: 10px;
  padding-right: 10px;
  overflow: hidden;
  opacity: 0;
  z-index: 2;
  transform-origin: center;
}
.tooltipImg {
  height: 40px;
}
.hoverToolTip .title {
  font-size: 20px;
  margin-left: 5px;
  margin-right: 5px;
}
.connection {
  border: 7px black;
  border-radius: 50px;
}

._main {
  position: absolute;
  display: flex;
  z-index: 1;
  height: 100vh;
  width: 50vw;
  right: 0px;
}
